.gallery-page {
  background-color: #ffffff;
}

.gallery-page__description {
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  color: #283645;

  margin-top: 50px;
  padding: 10px 30px 35px;
}

@media (min-width: @tablet-width) {
  .gallery-page {
    min-width: @tablet-width;
  }

  .gallery-page__description {
    font-size: 18px;
    padding-left: 60px;
  }
}
