.page-header {
  background-color: rgba(0, 0, 0, 0.4);
  position: relative;
  z-index: 2;
}

.page-header__logo-image {
  margin: 15px;


  &:hover {
    color: #d22856;
  }
}

.page-header__logo--icon-tablet,
.page-header__logo--icon-desktop {
  display: none;
}

.page-header__logo--icon-mobile use,
.page-header__logo--icon-tablet use,
.page-header__logo--icon-desktop use {
  fill: #ffffff;
}

.page-header__logo {
  &:hover {
    .page-header__logo--icon-mobile use,
    .page-header__logo--icon-tablet use,
    .page-header__logo--icon-desktop use {
      fill: #d22856;
    }
  }
  &:active {
    .page-header__logo--icon-mobile use,
    .page-header__logo--icon-tablet use,
    .page-header__logo--icon-desktop use {
      fill: #6c7682;
    }
  }
}

@media (min-width: @tablet-width) {
  .page-header__logo-image {
    height: 50px;
    width: 150px;
  }
  .page-header__logo--icon-mobile {
    display: none;
  }
  .page-header__logo--icon-tablet {
    display: inline-block;
  }
}

@media (min-width: @desktop-width) {
  .page-header__wrapper {
    background-color: transparent;
    width: 1000px;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    align-items: center;
    padding: 30px;

    position: relative;
    margin-bottom: -100px;
  }
  .page-header__logo--icon-tablet {
    display: none;
  }
  .page-header__logo--icon-desktop {
    display: inline-block;
  }
}
