@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
}
html {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  box-sizing: border-box;
  list-style: none;
}
body {
  margin: 0;
  min-width: 320px;
  background-color: #2a3948;
  /*скорее всего нарушает базовый критерий, однако предлагаю отложить до этапа полировки т.к.
  при верном глобальном написании ломает другие блоки с отступами*/
}
*,
*::before,
*::after {
  box-sizing: inherit;
  list-style: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* main-page */
.page-header {
  background-color: rgba(0, 0, 0, 0.4);
  position: relative;
  z-index: 2;
}
.page-header__logo-image {
  margin: 15px;
}
.page-header__logo-image:hover {
  color: #d22856;
}
.page-header__logo--icon-tablet,
.page-header__logo--icon-desktop {
  display: none;
}
.page-header__logo--icon-mobile use,
.page-header__logo--icon-tablet use,
.page-header__logo--icon-desktop use {
  fill: #ffffff;
}
.page-header__logo:hover .page-header__logo--icon-mobile use,
.page-header__logo:hover .page-header__logo--icon-tablet use,
.page-header__logo:hover .page-header__logo--icon-desktop use {
  fill: #d22856;
}
.page-header__logo:active .page-header__logo--icon-mobile use,
.page-header__logo:active .page-header__logo--icon-tablet use,
.page-header__logo:active .page-header__logo--icon-desktop use {
  fill: #6c7682;
}
@media (min-width: 660px) {
  .page-header__logo-image {
    height: 50px;
    width: 150px;
  }
  .page-header__logo--icon-mobile {
    display: none;
  }
  .page-header__logo--icon-tablet {
    display: inline-block;
  }
}
@media (min-width: 940px) {
  .page-header__wrapper {
    background-color: transparent;
    width: 1000px;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    align-items: center;
    padding: 30px;
    position: relative;
    margin-bottom: -100px;
  }
  .page-header__logo--icon-tablet {
    display: none;
  }
  .page-header__logo--icon-desktop {
    display: inline-block;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0px;
  /*-50px*/
  right: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.main-nav--closed {
  position: relative;
  margin-top: -50px;
  z-index: 2;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  z-index: 2;
  position: absolute;
  top: 14px;
  right: 10px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #5b3149;
  box-shadow: 0 8px 0 0 #5b3149, 0 16px 0 0 #5b3149;
}
/*-------------------иконка для открытого меню-----------------*/
.main-nav--opened .main-nav__toggle {
  top: -50px;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 6px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #5b3149;
}
.main-nav__list {
  list-style: none;
  font-size: 18px;
  font-weight: 700;
  background-color: rgba(0, 0, 0, 0);
  padding: 0;
  margin: 0;
}
.main-nav__item {
  text-align: center;
}
.main-nav__item:nth-child(1) {
  border-top: 1px solid #000000;
  box-shadow: 0 -1px 0 #485461;
}
.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  padding: 20px 0;
  display: block;
  padding-top: 20px;
  border-bottom: 1px solid #000000;
  box-shadow: 0 1px 0 #485461;
}
.main-nav__link:hover {
  color: #d22856;
}
.main-nav__link:active {
  color: #6a757f;
}
@media (max-width: 938px) {
  .main-nav--closed .main-nav__list {
    display: none;
    /* убирает main-nav при наличии класса closed для моб и таблет*/
  }
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
  .main-nav--no-js .main-nav__list {
    display: block;
    /*если js нет класс не будет удален, и не будет удалено меню с кнопкой*/
  }
}
@media (min-width: 940px) {
  .main-nav__list {
    display: flex;
    justify-content: space-around;
    width: 640px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__link {
    text-transform: capitalize;
    box-shadow: none;
    border-bottom: none;
    padding: 0;
  }
  .main-nav__item:nth-child(1) {
    border-top: none;
    box-shadow: none;
  }
}
