}
.price__table::before {
  background-image: linear-gradient(180deg, #283645 96px, #ffffff 97px, #ffffff 158px, #e5e5e5 159px, #e5e5e5 160px, #ffffff 161px, #ffffff 219px, #e5e5e5 220px, #e5e5e5 221px, #ffffff 222px, #ffffff 280px, #e5e5e5 280px, #e5e5e5 281px, #f2f2f2 282px);
  content: "";
  height: 283px;
  position: absolute;
  top: 2px;
  left: -20px;
  width: 18px;
}
.price__table::after {
  background-image: linear-gradient(180deg, #283645 96px, #ffffff 97px, #ffffff 158px, #e5e5e5 159px, #e5e5e5 160px, #ffffff 161px, #ffffff 219px, #e5e5e5 220px, #e5e5e5 221px, #ffffff 222px, #ffffff 280px, #e5e5e5 280px, #e5e5e5 281px, #f2f2f2 282px);
  content: "";
  height: 283px;
  position: absolute;
  top: 2px;
  right: -20px;
  width: 18px;
}
.price__value {
  font-weight: 300;
  font-size: 24px;
}
.price__title-cell {
  border: 2px solid #e5e5e5;
  padding: 20px 0;
  background-color: #283645;
  color: #ffffff;
  font-size: 18px;
  text-transform: uppercase;
  position: relative;
}
.price__title-cell--none {
