  border: none;
}
.price__title-cell--hit::before {
  content: url("../img/price/bg-hit.svg");
  position: absolute;
  left: 0;
  top: 0;
}
.price__title-cell--hit::after {
  content: "ХИТ";
  font-size: 14px;
