  /*-------------////main data--------*/
  .contest__radio-tile {
    /*----------radio-------------*/
    display: block;
  }
  .contest__group-radio {
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    order: 2;
    background-color: #ffffff;
    border: 2px solid #e5e5e5;
    position: static;
  }
  .contest__group-radio .contest__radio-tile {
    position: static;
  }
  /*------------///radio---------*/
  .contest__checkbox-list {
    /*-------------checkbox---------*/
    padding-top: 0;
  }
  .contest__group-checkbox {
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    order: 4;
    background-color: #ffffff;
    position: static;
    border: 2px solid #e5e5e5;
  }
  .contest__checkbox-title {
    position: static;
  }
  /*----------/// checkbox---------*/
  .contest__group-area {
    /*-------------group area-------*/
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 2px solid #e5e5e5;
    order: 6;
  }
  /*-------------/// group area------------*/
  .contest__adress-title {
    /*------------ adress-data---------*/
    display: block;
  }
  .contest__adress-data {
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    order: 3;
    border: 2px solid #e5e5e5;
  }
  .contest__adress-data .contest__adress-item {
    display: flex;
    flex-direction: column-reverse;
    position: relative;
    margin-top: 0;
  }
  .contest__adress-data .contest__adress-item--tel::after,
  .contest__adress-data .contest__adress-item--email::after {
    content: "";
    background-image: url("../img/contest/icon-phone.svg");
    background-color: #e5e5e5;
    background-repeat: no-repeat;
    background-size: 25px 25px;
    background-position: center;
    position: absolute;
    width: 62px;
    height: 65px;
    right: 50px;
    top: 30px;
  }
  .contest__adress-data .contest__adress-item--email::after {
    background-image: url("../img/contest/icon-mail.svg");
  }
  .contest__adress-data .contest__text {
    margin-top: 10px;
  }
  .contest__adress-data .contest__input {
    margin-top: 30px;
  }
  /*--------------------///-adress-data--------------------*/
  .contest__submit {
    order: 6;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    width: 100%;
    margin-bottom: 40px;
  }
}
@media (min-width: 940px) {
  .contest {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .contest__main-data {
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
  }
  .contest__main-data .contest__main-item:nth-child(1) {
    margin-top: 0;
  }
  .contest__group-radio {
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
  }
  .contest__radio-list {
    width: 300px;
    margin-top: 0;
  }
  .contest__radio-description {
    margin-top: 15px;
  }
  .contest__adress-data {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
  }
  .contest__adress-data .contest__adress-list {
    flex-direction: row;
    justify-content: space-around;
  }
  .contest__group-checkbox {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .contest__checkbox-list {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .contest__checkbox-item {
    width: 250px;
  }
  .contest__group-area {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 40px 40px 40px;
  }
}
